import React from 'react';
import JobList from './JobList';

const FeatureList = ({ title, clickAddJob, data, openModal }) => (
  <div className={title + ' aux'}>
    <h3>
      <span className="counter">{(data && data.length) || 0}</span> {title}
    </h3>
    <div onClick={() => clickAddJob(title)} className="add_new">
      &#43; Add job
    </div>
    <JobList data={data} openModal={() => openModal(title)} />
  </div>
);
export default FeatureList;
